import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: 'evDealer',
    loadChildren: () => import('./EV-dealers/EV-dealers.module').then(m => m.EVDealersModule),
    data: { title: 'EV Dealers', titleI18n: 'EV-Dealers' },
  },
  {
    path: 'govtSubsidy',
    loadChildren: () => import('./subsidy/subsidy.module').then(m => m.SubsidyModule),
    data: { title: 'Govt Subsidy', titleI18n: 'Govt Subsidy' },
  },
  {
    path: 'evFleet',
    loadChildren: () => import('./EV-fleet/EV-fleet.module').then(m => m.EVFleetModule),
    data: { title: 'Ev Fleet', titleI8n: 'Ev Fleet' }
  },
  {
    path: 'evComponentsManufactures',
    loadChildren: () => import('./EV-components -manufacturers/EV-components-manufacturers.module').then(m => m.EVComponentsManufacturersModule),
    data: { title: 'EV Components Manufactures', titleI18n: 'EV Components Manufactures' },
  },


]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class EVSpaceRoutingModule { }